// * MODULES - reset, normalize, customize
// * ---------------------------------------
@import "./config/fingo-colors";
@import "./Modules/sass/style";
@import "./config/fingo-config";
@import "./config/fingo-reset";

input {
  @extend %reset-border-box; }
button {
  @extend %reset-button; }

// .dim
.dim,
.dim2 {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba($fingo-bg-dark, 0.8);
  z-index: 100000; }
.dim2 {
  background: rgba($fingo-bg-dark, 0.7); }

// .modal-window
.modal-window {
  @include position(fixed, top 50% left 50%);
  @include background(color #222);
  @include size(620px 310px);
  @include padding(top 20px left 30px right 30px);
  align-items: center;
  border-radius: 5px;
  transform: translate(-50%, -50%);
  box-shadow: 0 0 20px 2px hsla(0,0%,0%,.45);
  z-index: 100010;

  > * {
    margin: 0; }
  .modal-headline {
    @include padding(bottom 10px);
    display: inline-block;
    position: relative;
    color: $fingo-white;
    font-size: 18px;
    .modal-rank-btn {
      @include margin(left 10px);
      @include size(15px);
      &::before {
        content: '';
        @include position(absolute, top 5px right 0);
        width: 10px;
        height: 10px;
        transform: rotate(-135deg);
        border: {
          top: 2px solid $fingo-mian-color;
          left: 2px solid $fingo-mian-color; } } } }
  .modal-contents {
    @include padding(top 20px left 100px);
    border-top: 1px solid rgba($fingo-white, 0.3);
    .modal-poster {
      @include position(absolute, left 30px);
      @include size(80px 115px); }
    .modal-stars {
      color: $fingo-white; }
    .modal-textarea {
      @include margin(top 10px);
      @include size(100% 130px);
      @include background(color #222);
      font-size: 14px;
      color: $fingo-white;
      border: 0;
      outline: 0; }
    .modal-ok-button {
      @include size(100px 40px);
      @include background(color $fingo-bg-dark);
      float: right;
      line-height: 40px;
      border-radius: 5px;
      color: $fingo-white;
      font-size: 16px;
      &:hover {
        @include background(color $fingo-mian-color); } } }
  .modal-close-button {
    @include position(absolute, top 20px right 30px);
    width: 30px;
    height: 30px;
    line-height: 14px;
    color: #555;
    font-size: 30px;
    &:hover {
      @extend %focus-state; } }

  .modal-rank-contents {
    @include position(absolute, top 55px left 30px z-index 10000000);
    @include size(220px 230px);
    @include padding(top 20px bottom 20px left 30px right 30px);
    @include background(color #000);
    text-align: center;
    border: 1px solid #000;
    border-radius: 5px;
    color: $fingo-white;
    &::before {
      content: '';
      @include position(absolute, top -10px right 75px);
      @include size(0);
      border-left: 5px solid transparent;
      border-right: 5px solid transparent;
      border-bottom: 10px solid #000; }
    h4 {
      @include padding(bottom 10px);
      font-size: 14px;
      color: #cacaca;
      border-bottom: 1px solid #444; }
    p {
      @include margin(top 20px bottom 7px);
      font-size: 14px;
      color: #cacaca;
      font-weight: lighter; }
    button {
      @include size(100px 35px);
      border: 1px solid $fingo-white;
      color: $fingo-white;
      line-height: 33px;
      &.active {
        @include background(color $fingo-heart);
        border: 1px solid $fingo-heart; }
      &:hover {
        @include background(color $fingo-heart);
        border: 1px solid $fingo-heart; } } } }
