// 컨테이너(.container) 너비
$m-container-width: 640px;
$t-container-width: 964px;
$d-container-width: 1240px;

$bp-mobile: 720px;
$bp-tablet: 1024px;

// * ------------------------------------------------
// * Reset Component
// * ------------------------------------------------
button {
  transition: all 0.2s ease-in-out;
  @extend %reset-button; }
a {
  transition: all 0.2s ease-in-out; }

body {
  font-family: 'Spoqa Han Sans', 'Sans-serif';
  @include size(100% auto); }


// * ------------------------------------------------
// * Main page - Container
// * ------------------------------------------------
.container {
  @include size(1240px auto);
  @include margin(0 auto);
  @include tablet {
    width: percentage($t-container-width/$bp-tablet); }
  @include mobile {
    width: percentage($m-container-width/$bp-mobile); } }
